import React, { Component } from 'react';
// import ReactDOM from 'react-dom';
import './Info.scss';
import { Card, Accordion } from 'react-bootstrap';
import InfoCard from '../info-card/Info-card';
import Header from './header/Header';
import Footer from './footer/Footer';
class Info extends Component {
    constructor()
  {
    super();
    this.state={
      stats: [],
    }
  }
  componentDidMount()
  {
    fetch('https://api.covid19india.org/data.json').then(response => response.json()).then((jsonData) => {
    //console.log(jsonData.statewise);
    const dcount = jsonData.statewise;
    
    //const i = 1;
    //console.log(dcount);
          this.setState({
            stats:dcount,
          })
      //console.log(this.state.active);

  }).catch((error) => {
    console.error(error)
  })
  }


    render(){
        const { stats } = this.state;
        // const arrayavaratcard = dcount.map( (avatarcard, i) => {
        //     return <div key={i}>
        //             <h3 className="bg-warning p-2 rounded"> <strong>{this.state.state} </strong> <span className="float-right">As on: {this.state.lastupdatedtime}</span></h3>
        //             <div className="row">
        //                 <InfoCard name="Active" info={this.state.active} colorName="bg-info"  />
        //                 <InfoCard name="Confirmed" info={this.state.confirmed} colorName="bg-warning" />
        //                 <InfoCard name="Deaths" info={this.state.deaths} colorName="bg-danger "/>
        //                 <InfoCard name="Recovered" info={this.state.recovered} colorName="bg-success"/>
        //             </div>
        //         </div>
        // })
        const dak = 0;
        const arrayavaratcard = stats && stats.map( (avatarcard, i) => {
            //console.log(i);
            return <Card key={i}>
                    <Accordion.Toggle as={Card.Header} className="acc-head" eventKey={i}>
                    <h3 className="p-2 rounded"> <strong>{avatarcard.state} </strong> <span>As on: {avatarcard.lastupdatedtime}</span></h3>
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey={i}>
                      <Card.Body>
                      <div className="row">
                          <InfoCard name="Active" info={avatarcard.active} colorName="bg-info"  />
                          <InfoCard name="Confirmed" info={avatarcard.confirmed} colorName="bg-warning" />
                          <InfoCard name="Deaths" info={avatarcard.deaths} colorName="bg-danger "/>
                          <InfoCard name="Recovered" info={avatarcard.recovered} colorName="bg-success"/>
                      </div>
                      </Card.Body>
                    </Accordion.Collapse>
                </Card>
                
        })
        return (
          <div>
            <Header/>
            <div className="container-fluid">
                <Accordion className="mb-3"  defaultActiveKey={dak}>
                   {arrayavaratcard}
                </Accordion>
            </div>
            <Footer/>
          </div>
        ) 
    }
}

export default Info;